import React, { useState } from 'react'
import { ArrowLeft } from 'react-bootstrap-icons'
import { useHistory } from 'react-router'
import './Register.css'

// - NOMBRE FANTASIA
// - CUIT
// - TELEFONO
// - Celular wasapp(OBLIGATORIO)
// - PROBVNCIA,
// - CIUDAD(SI SE PUEDE TIPO COMBO)
// - UN BOTON PARA ENVIAR LOS DATOS
const Register = ({ theme }) => {
    const history = useHistory()
    const [datos, setDatos] = useState({
        empresa: "",
        cuit: "",
        telefono: "",
        ubicacion: ""
    })
    const handleInputChange = (e) => {
        setDatos({
            ...datos,
            [e.target.name]: e.target.value
        })
    }
    const handleSubmit = (event) => {
        history.push("/registered")
    }
    return (<div className="registerContainer">
        <div classname="formulario">
            <form onSubmit={handleSubmit}>
                <div className="col">
                    <p className='titulo'>EMPRESA</p>
                    <input type="text" placeholder="ej: CMPCP S.A." className={"form-input btn btn" + theme + " " + theme} onChange={handleInputChange} name="empresa"></input>
                </div>
                <div className='col'>
                    <p className='titulo'>CUIT</p>
                    <input type="text" placeholder="ej: 11-11111111-1" className={"form-input btn btn" + theme + " " + theme} onChange={handleInputChange} name="cuit"></input>
                </div>
                <div className='col'>
                    <p className='titulo'>TELEFONO</p>
                    <input type="tel" placeholder="Whatsapp" className={"form-input btn btn" + theme + " " + theme} onChange={handleInputChange} name="telefono"></input>
                </div>
                <div className='col'>
                    <p className='titulo'>LOCALIDAD</p>
                    <input type="text" placeholder="ej: Rosario, Santa Fe, Argentina" className={"form-input btn btn" + theme + " " + theme} onChange={handleInputChange} name="ubicacion"></input>
                </div>
                <div className="col" style={{ marginTop: "20vh" }}>
                    <button style={{ padding: "20px 10px", fontSize: "2vh" }} className={"btn btn" + theme + " " + theme} type="submit" >COMPLETAR REGISTRO</button>
                </div>
            </form>
        </div>
        <button className={"btn btn" + theme + " btnBack " + theme} onClick={() => {
            const element = document.querySelector(".formulario")
            element.style.animation = "haciaAbajo 0.5s 1"
            setTimeout(() => {
                return history.goBack()
            }, 400);
        }}><ArrowLeft /></button>
    </div>)
}
export default Register;